@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}*{margin:0;padding:0}.container{background:linear-gradient(100deg, #b6286f 50%, #ac2066 0);width:100%;height:100vh;display:flex;justify-content:center;align-items:center}.app-wrapper{background-color:#292f38;width:25%;min-width:450px;height:600px;padding:30px;box-sizing:border-box;border-radius:5px;box-shadow:0 15px 30px rgba(0,0,0,0.4)}.header h1{color:#ccc;font-weight:300;text-align:center;margin:50px 20px 60px 20px;font-family:'Josefin Slab', serif}.main{display:flex;flex-direction:column;align-items:space-between;margin-bottom:50px;width:100%}.form{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:10px}.list{width:90%;margin:auto;max-height:300px;overflow:hidden;overflow-y:auto}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{border-radius:10px;background-color:#aaa}::-webkit-scrollbar-thumb{background:#444;border-radius:10px}.task-input{width:350px;min-width:250px;padding:15px 10px 10px 20px;margin-right:10px;font-family:'Josefin Sans', serif;font-size:15px;color:#ccc;background-color:#1d222b;border:1px solid #444;outline:none;border-radius:20px;box-shadow:0 1px 2px rgba(0,0,0,0.3)}.buttons{display:flex}.btn{margin:10px}.add-task-btn{width:100px !important;height:35px !important;background-color:#790e43 !important;color:#ccc !important;border:none !important;font-family:'Montserrat', sans-serif !important;font-size:14px !important;border-radius:20px !important;box-shadow:0 2px 5px rgba(0,0,0,0.3) !important;outline:none !important;cursor:pointer !important}.list-item{display:flex;justify-content:space-between;margin:20px 0;padding-bottom:5px}.list-item span{color:#999}.task-btn{cursor:pointer;background-color:transparent;border:none;outline:none}.btn-delete i{font-size:16px;color:#bbb;margin-right:20px}.btn-edit i{font-size:16px;color:#bbb;margin-right:20px}.clear-btn{width:100px !important;height:35px !important;background-color:#790e43 !important;color:#ccc !important;border:none !important;font-family:'Montserrat', sans-serif !important;font-size:14px !important;border-radius:20px !important;box-shadow:0 2px 5px rgba(0,0,0,0.3) !important;outline:none !important;cursor:pointer !important}.no-tasks{color:#777;text-align:center;font-size:18px;margin-top:20px}

